body {
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: rgb(32, 32, 32);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

header {
  width: 100%;
  height: 80px;
  background-color: rgb(4, 33, 87);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

nav {
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

nav ul {
  margin: 0;
  padding: 0;
}

.header-homelink {
  margin: 0 2em;
  color: whitesmoke;
  text-decoration: none;
  font-size: 1.5em;
  font-weight: 600;
}

.header-link {
  margin: 0 2em;
  text-decoration: none;
  color: #fff;
  font-weight: 600;
  border-bottom: 2px solid transparent;
  display: inline-block;
  -webkit-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

.header-link:hover {
  color: rgb(255, 0, 64);
}


